  color: #464646;
  font-size: 15px;
  line-height: 25px;
  text-transform: uppercase;
  position: relative;
}

.main-menu ul li a::before, .main-menu ul li a::after {
  position: absolute;
  content: '';
  width: 4px;
  height: 7px;
  top: 50%;
  transform: translateY(-50%);
}

.main-menu ul li a::before {
  left: 0;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 0 7px 4px;
  border-color: transparent transparent #c3d8ed transparent;
}

.main-menu ul li a::after {
  left: 4px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 7px 4px 0 0;
  border-color: #c3d8ed transparent transparent transparent;
}

.main-menu ul li a:hover, .main-menu ul li a:focus, .main-menu ul li a:active {
  color: #464646;
  text-decoration: none;
