.main-menu ul li a:hover::before, .main-menu ul li a:focus::before, .main-menu ul li a:active::before {
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 0 7px 4px;
  border-color: transparent transparent #86b0da transparent;
}

.main-menu ul li a:hover::after, .main-menu ul li a:focus::after, .main-menu ul li a:active::after {
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 7px 4px 0 0;
