.toggle {
  display: none;
  width: 35px;
  height: 21px;
  padding: 9px 0;
  z-index: 102;
  position: relative;
  float: right;
}

@media only screen and (max-width: 992px) {
  .toggle {
    display: block;
  }
}

.toggle span {
  position: relative;
  display: block;
  width: 100%;
  height: 3px;
  background-color: #39f;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
}

.toggle span:after, .toggle span:before {
  content: '';
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #39f;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
}

.toggle span:after {
  top: 9px;
}

.toggle span:before {
  top: -9px;
}

.toggle.on span {
  background-color: transparent;
}

.toggle.on span:before {
  transform: rotate(45deg) translate(6px, 6px);
}

.toggle.on span:after {
  transform: rotate(-45deg) translate(6px, -7px);
}

.toggle.on + .main-menu {
  opacity: 1;
  visibility: visible;
  transition: opacity .4s;
  z-index: 101;
}

