    position: absolute;
    width: 100%;
    top: 42px;
    left: 0;
    text-align: left;
    background-color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: opacity .4s;
    z-index: -1;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.15); }
  ul {
    display: flex;
    justify-content: flex-start;
    @include r(992) {
