      padding-left: 20px; }
    li {
      a {
        display: inline-block;
        padding-left: 17px;
        margin-right: 8px;
        color: #464646;
        font-size: 15px;
        line-height: 25px;
        text-transform: uppercase;
        position: relative;
        &::before,
        &::after {
          position: absolute;
          content: '';
          width: 4px;
          height: 7px;
          top: 50%;
          transform: translateY(-50%); }
        &::before {
          left: 0;
          @include arr(4,7, #c3d8ed, br); }
        &::after {
          left: 4px;
          @include arr(4,7, #c3d8ed, tl); }
        &:hover,
        &:focus,
        &:active {
          color: #464646;
          text-decoration: none;
          &::before {
            @include arr(4,7, #86b0da, br); }
          &::after {
            @include arr(4,7, #86b0da, tl); } } } } } }
.toggle {
  display: none;
  width: 35px;
  height: 21px;
  padding: 9px 0;
  z-index: 102;
  position: relative;
  float: right;
  @include r(992) {
    display: block; }
  span {
    position: relative;
    display: block;
    width: 100%;
    height: 3px;
    background-color: #39f;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px;
    &:after,
    &:before {
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #39f;
      transition: all 0.3s;
      backface-visibility: hidden;
      border-radius: 2px; }
    &:after {
      top: 9px; }
    &:before {
      top: -9px; } }
  &.on {
    span {
      background-color: transparent;
      &:before {
        transform: rotate(45deg) translate(6px, 6px); }
      &:after {
        transform: rotate(-45deg) translate(6px, -7px); } }
    + .main-menu {
      opacity: 1;
      visibility: visible;
      transition: opacity .4s;
      z-index: 101; } } }

