.shop-thumb {
  margin-bottom: 20px;
  padding: 5px;
  border: 1px solid #f2f2f2;
  margin-bottom: 0; }
.shop-features {
  margin-bottom: 20px;
  * {
    vertical-align: middle; } }
.shop-features-title,
.shop-features-descr {
  display: inline-block;
  color: #000;
  font-size: 14px; }
.shop-features-title {
  width: 40%;
  @include r(576) {
    width: 100%;
    font-weight: 700; } }
.shop-features-descr {
  width: 60%;
  @include r(576) {
    width: 100%;
    margin-bottom: 5px; }
  img {
    margin-right: 5px; } }
.st-accent {
  color: #9cb407;
  font-weight: 700; }



