.shop-button-prev,
.shop-button-next {
  position: absolute;
  width: 12px;
  height: 18px;
  top: 50%;
  transform: translateY(-50%);
  background-image: url(../img/slider-sprite.png);
  background-repeat: no-repeat; }
.shop-button-prev {
  left: 0;
  background-position: 0 0;
  &:hover {
    background-position: 0 -18px; } }
.shop-button-next {
  right: 0;
  background-position: -12px 0;
  &:hover {
    background-position: -12px -18px; } }

