body {
  background: #e6e9ec;
  color: #1b1b1b;
  font-family: 'PT Sans', sans-serif;
  font-size: 15px;
  font-weight: 400; }

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  font-weight: 700;
  color: #2d2d2d;
  margin: 1em 0; }
h1, .h1 {
  font-size: 30px;
  margin-top: 0;
  margin-bottom: 22px;
  line-height: 34px;
  span {
    color: #39f; } }
h2, .h2 {
  font-size: 24px; }
h3, .h3 {
  font-size: 18px; }
h4, .h4 {
  font-size: 16px; }
h5, .h5 {
  font-size: 15px; }
h6, .h6 {
  font-size: 14px; }

.cnt-title {
  text-transform: uppercase; }
.cnt-descr {
  margin-bottom: 25px; }

p {
  margin: 25px 0;
  line-height: 24px; }

img {
  max-width: 100%;
  vertical-align: middle; }

a {
  color: #39f;
  text-decoration: underline;
  &:hover,
  &:active,
  &:focus {
    color: #39f;
    text-decoration: none; } }

