.single-content {
  img {
    padding: 30px;
    border: 0;
    margin-bottom: 1em; } }


blockquote {
  display: table;
  margin: 25px 0;
  padding: 15px 28px;
  background-color: rgba(#dcdbc4, .4); }

.block {
  background-color: #fff;
  margin-bottom: 20px; }

.block-bg {
  background-color: #f6f2f6;
  padding: 65px 35px 30px; }

.block-bs {
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.15); }

.block-item {
  padding: 18px;
  border-bottom: 1px solid #e5e5e5; }

.custom-widgets {
  background-color: #fff; }

.breadcrumbs-wr {
  padding: 20px 0;
  background-color: #f6f2f6;
  @include r(768) {
    display: none; } }

.breadcrumbs {
  font-family: 'Roboto';
  font-size: 13px;
  font-weight: 400;
  line-height: 24px;
  color: #333;
  ul {
    margin: 0;
    padding: 0;
    li {
      display: inline-block;
      &::before {
        display: none; } } }
  a {
    color: #39f;
    text-decoration: underline;
    &:hover,
    &:active,
    &:focus {
      color: #39f;
      text-decoration: none; } }
  .kb_sep {
    color: #999;
    margin: 0 5px; } }


.bg-wr {
  position: relative;
  &::before,
  &::after {
    position: absolute;
    content: '';
    left: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    z-index: -2; }
  &::before {
    background-image: url("../img/bg-top.png");
    background-position: center top;
    top: 0; }
  &::after {
    background-image: url("../img/bg-bottom.png");
    background-position: center bottom;
    bottom: 0; } }
.cnt-wr {
  position: relative; }
